/* Page background */
body.woocommerce.single-product
  @apply bg-background
  div.product
    /* Summary column */
    div.summary
      @apply float-none w-full max-w-full
    /* Title */
    h1.product_title
      @extend h2
      @apply mb-1
    /* SKU */
    span.product-sku
      @apply text-dark font-barlow font-medium text-sm mb-8 block
    /* Short description */
    .woocommerce-product-details__short-description
      @extend .block-text
      @apply mb-12 text-blockText text-lg font-normal leading-[1.8562rem] xl:max-w-[90%]
      p
        @extend .block-text
        @apply text-blockText text-lg font-normal leading-[1.8562rem] mb-8 last-of-type:mb-0
    /* Price block spacing (divider) */
    .price
      @apply border-t border-t-dark pt-10 text-dark font-barlow font-normal text-4xl uppercase mb-9
    .stock.out-of-stock
      @extend .block-text
      @apply text-red-700 font-semibold mt-4
    /* Price typography */
    .woocommerce-Price-currencySymbol
      @apply font-normal
    .woocommerce-Price-amount
      @apply font-bold
    form.cart
      @apply flex items-center
      .quantity
        @apply flex items-stretch
      .single_add_to_cart_button
        @extend .btn
        @extend .btn-secondary
        @apply rounded-none flex items-center justify-center text-center px-16 py-[0.375rem] font-medium text-base ml-5


  div.product-type-wgm_gift_card
    form.cart
      @apply grid grid-cols-4
      .wps_wgm_added_wrapper
        @apply col-span-4 my-9
        .wps_wgm_section
          @extend .block-text
          @apply text-dark mb-4
          input,
          textarea,
          select
            @apply px-3 py-4 font-barlow
          .wps_wgc_label
            @apply font-barlowSemiCondensed font-medium text-base mb-2 inline-block
          .wps_wgm_message_length
            @extend .block-text
            @apply text-dark text-sm
        .wps_delivery_method
          .wps_wgm_delivery_method
            @apply border-none shadow bg-gray-300
          .wps_wgm_method
            @extend .block-text
          .wps_wgm_delivery_via_buyer
            @apply my-2
            .wps_wgm_msg_info
              @extend .block-text
              @apply text-dark text-sm
        .mwg_wgm_preview_email
          @apply text-dark font-barlow text-[12px] md:text-[18px] font-bold leading-[8.811px] md:leading-[13px]
      .price
        ins
          @apply no-underline
          .woocommerce-Price-amount.amount
            @apply flex gap-x-4
      .ab-qty
        @apply col-span-1
      .single_add_to_cart_button
        @apply col-span-3 w-full ml-0 font-medium text-base

  div.product-type-variable
    .price
      span
        @apply hidden
    form.variations_form.cart
      @apply grid grid-cols-4
      table.variations
        @apply col-span-4 w-full
        tr
          @apply flex flex-col
          th.label
            @apply flex
            label
              @apply font-barlow font-medium text-dark
          td.value
            @apply flex items-center
            select
              @apply px-3 py-4 font-barlow bg-white
            a.reset_variations
              @apply font-barlow text-xs text-dark uppercase font-medium
      .single_variation_wrap
        @apply col-span-4 grid grid-cols-4
        .single_variation
          @apply mb-4
        .variations_button
          @apply col-span-4 flex